header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* -------------- Call To Action (CTA) --------------- */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* -------------- Header Social Buttons --------------- */
.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* -------------- My Image (ME) --------------- */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 32.8rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
}

/* -------------- Scrolling Down --------------- */
.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

/* ---------- Media Queries : Medium Devices ----------- */
@media screen and (max-width: 1024px) {
    .container {
        height: 68vh;
    }
}
/* ---------- Media Queries : Small Devices ----------- */
@media screen and (max-width: 600px) {
    .container {
        height: 100vh;
    }
    
    .header_socials, .scroll_down {
        display: none;
    }
}








